    public addProperty(name: string, displayName: string, propertyDataType: PropertyDataType): void {
        let propertyEntity =
        {
            text: displayName,
            propertyDataType: propertyDataType,
            value: name,
            parentItem: null
        };
        this.propertyList = this.propertyList.filter(item => item.value !== name);
        this.propertyList.push(propertyEntity);
    }

