import { FilterInjectedEntity, FilterInjectedType } from "./injected-entity";
import { FilterEntitySchema, FilterEntityType, FilterEntitySet } from "./schema";
import { NavigationType } from "./navigation-property-entity";
import { PropertyDataType } from ".";
import { NewDataType, NewEntitySet, NewEntityType, NewNavigationType, NewSchema } from "./NewSchema";

export class PropertyAdapter {
    entityName: string;
    /**
     * å±æ§ééå¨ï¼æ³¨å¥å¤çº§å®ä½è½¬åä¸ºArray
     * @param injectedEntity æ³¨å¥çå®ä½
     * @param schemaMap æ³¨å¥çschema
     */
    adapter(injectedEntity: FilterInjectedEntity, schemaMap: Map<string, FilterEntitySchema>) {
        //æ ¹æ®æ³¨å¥schemaåå¾å°schema
        let schema = schemaMap.get(injectedEntity.schemaName);
        //æ ¹æ®æ³¨å¥ç±»ååå«è¿è¡è½¬å
        switch (injectedEntity.injectedType) {
            case FilterInjectedType.EntitySet:
                {
                    let entitySet = schema.entitySets.find(entitySet => entitySet.name == injectedEntity.name);
                    return this.convertEntitySet(entitySet, schema);
                }
            case FilterInjectedType.EntityType:
                {
                    let entityType = schema.entityTypes.find(entityType => entityType.name == injectedEntity.name);
                    return this.convertEntityType(entityType, schema);
                }
            default: break;
        }
    }
    /**
     * å°å®ä½ç±»è½¬åæArrray
     * @param entityType è¢«è½¬æ¢çå®ä½ç±»
     */
    convertEntityType(entityType: FilterEntityType, schema: FilterEntitySchema): any {
        let propertyList = Array<{ text: string, propertyDataType: PropertyDataType, value: string, parentItem: string }>();
        //å¸¸è§å±æ§è½¬å
        entityType.properties.forEach(property => {
            let propertyListItem =
            {
                text: property.displayName,
                propertyDataType: property.propertyDataType,
                value: property.name,
                parentItem: null
            };
            propertyList.push(propertyListItem);
        });
        //å¯¼èªå±æ§è½¬å
        entityType.navigationProperties.forEach(navigationProperty => {
            //æ ¹æ®å¯¼èªå±æ§ç±»åè¿è¡è½¬å
            switch (navigationProperty.navigationType) {
                //è¥å¯¼èªå±æ§ä¸ºEntitySet
                case NavigationType.EntitySet:
                    {
                        //æ ¹æ®å¯¼èªå±æ§åæ¾å°schemaä¸­çEntitySet
                        let entitySet = schema.entitySets.find(entitySet => entitySet.name == navigationProperty.navigationEntity);
                        //å­å®ä½éè½¬åä¸ºArray
                        this.convertEntitySet(entitySet, schema).forEach(element => {
                            //è¥å­å®ä½ä¸­çæ¯ä¸ªå±æ§ç¶é¡¹ä¸ä¸ºç©ºï¼åå¨ç¶é¡¹ååæ·»å è¯¥å¯¼èªå±æ§åï¼ä¾å¦OrderItemåå Orderä¸ºOrders.OrderItem
                            if (element.parentItem != null) {
                                element.parentItem = navigationProperty.name + "." + element.parentItem;
                            }
                            //è¥ç¶é¡¹ä¸ºç©ºï¼åè®¾ç½®è¯¥å±æ§ç¶é¡¹ä¸ºå¯¼èªå±æ§å
                            else {
                                element.parentItem = navigationProperty.name;
                            }
                            //è®¾ç½®è¯¥å±æ§çæ¾ç¤ºåç§°
                            element.text = navigationProperty.displayName + "." + element.text;
                            propertyList.push(element);
                        });
                    } break;
                //è¥å¯¼èªå±æ§ç±»åä¸ºEntityType
                case NavigationType.EntityType:
                    {
                        //æ ¹æ®å¯¼èªå±æ§åæ¾å°schemaä¸­çEntityType
                        let entityType = schema.entityTypes.find(entityType => entityType.name == navigationProperty.navigationEntity);
                        //å­å®ä½è½¬åä¸ºArray
                        this.convertEntityType(entityType, schema).forEach(element => {
                            //è¥å­å®ä½ä¸­çæ¯ä¸ªå±æ§ç¶é¡¹ä¸ä¸ºç©ºï¼åå¨ç¶é¡¹ååæ·»å è¯¥å¯¼èªå±æ§åï¼ä¾å¦OrderItemåå Orderä¸ºOrder.OrderItem
                            if (element.parentItem != null) {
                                element.parentItem = navigationProperty.name + "." + element.parentItem;
                            }
                            //è¥ç¶é¡¹ä¸ºç©ºï¼åè®¾ç½®è¯¥å±æ§ç¶é¡¹ä¸ºå¯¼èªå±æ§å
                            else {
                                element.parentItem = navigationProperty.name;
                            }
                            //è®¾ç½®è¯¥å±æ§çæ¾ç¤ºåç§°
                            element.text = navigationProperty.displayName + "." + element.text;
                            propertyList.push(element);
                        });
                    }
                    break;
            }
        });
        return propertyList;
    }
    /**
     * å®ä½éè½¬åä¸ºå±æ§åè¡¨
     * @param entitySet å¾è½¬åå®ä½é
     * @param schema æå¨schema
     */
    convertEntitySet(entitySet: FilterEntitySet, schema: FilterEntitySchema): any {
        let propertyList = Array<{ text: string, propertyDataType: PropertyDataType, value: string }>();
        let entityType = schema.entityTypes.find(entityType => entityType.name == entitySet.entityTypeName);

        entityType.properties.forEach(property => {
            let propertyListItem =
            {
                text: property.displayName,
                propertyDataType: property.propertyDataType,
                value: property.name,
                parentItem: null
            };
            propertyList.push(propertyListItem);
        });
        if (entityType.navigationProperties.length != 0) {
            //å¯¼èªå±æ§è½¬å
            entityType.navigationProperties.forEach(navigationProperty => {

                switch (navigationProperty.navigationType) {
                    case NavigationType.EntitySet:
                        {
                            let entitySet = schema.entitySets.find(entitySet => entitySet.name == navigationProperty.navigationEntity);
                            this.convertEntitySet(entitySet, schema).forEach(element => {

                                if (element.parentItem != null) {
                                    element.parentItem = navigationProperty.name + "." + element.parentItem;
                                }
                                else {
                                    element.parentItem = navigationProperty.name;
                                }
                                element.text = navigationProperty.displayName + "." + element.text;
                                propertyList.push(element);
                            });
                        } break;
                    case NavigationType.EntityType:
                        {
                            let entityType = schema.entityTypes.find(entityType => entityType.name == navigationProperty.navigationEntity);
                            this.convertEntityType(entityType, schema).forEach(element => {

                                if (element.parentItem != null) {
                                    element.parentItem = navigationProperty.name + "." + element.parentItem;
                                }
                                else {
                                    element.parentItem = navigationProperty.name;
                                }
                                element.text = navigationProperty.displayName + "." + element.text;
                                propertyList.push(element);
                            });
                        }
                        break;
                }
            });
        }
        return propertyList;
    }

    //==========================åå²çº¿ï¼NewSchemaï¼============================================
    /**
     * 
     * @param injectedEntity 
     * @param newSchemaMap 
     */
    public adapterOfNewSchema(injectedEntity: FilterInjectedEntity, newSchemaMap: Map<string, NewSchema>) {
        //injectedEntity.schemaNameæ¾å°Schema
        let schema = newSchemaMap.get(injectedEntity.schemaName);

        //injectedEntity.injectedTypeç¡®å®æ¾setsè¿æ¯types
        switch (injectedEntity.injectedType) {
            case FilterInjectedType.EntitySet: {
                let set = schema.entitySets.find(set => set.code == injectedEntity.name);
                return this.convertNewEntitySet(set, schema);
            }
            case FilterInjectedType.EntityType: {
                let type = schema.entityTypes.find(type => type.code == injectedEntity.name);
                return this.convertNewEntityType(type, schema);
            }
            default:
                break;
        }
    }

    /**
     * @description è½¬æ¢EntityType
     * @param type NewEntityType
     * @param schema NewSchema
     * @returns 
     */
    convertNewEntityType(type: NewEntityType, schema: NewSchema): any {
        let propertyList = Array<{ text: string, propertyDataType: PropertyDataType, value: string, parentItem: string }>();
        //propertyè½¬æ¢
        type.properties.forEach(pro => {
            let propertyListItem =
            {
                value: pro.code,
                text: pro.name,
                propertyDataType: this.convertNewDataType2PropertyDataType(pro.dataType),
                parentItem: null
            };
            propertyList.push(propertyListItem);
        });
        //navigationPropertyè½¬æ¢
        type.navigationProperties.forEach(naPro => {
            switch (naPro.navigationType) {
                case NewNavigationType.EntitySet:
                    {
                        let naProSet = schema.entitySets.find(naProSet => naProSet.code == naPro.navigationName);
                        this.convertNewEntitySet(naProSet, schema).forEach(ele => {
                            if (ele.parentItem != null) {
                                ele.parentItem = naPro.code + "." + ele.parentItem;
                            } else {
                                ele.parentItem = naPro.code;
                            }
                            ele.text = naPro.name + "." + ele.text;
                            propertyList.push(ele);
                        });
                    }
                    break;
                case NewNavigationType.EntityType:
                    {
                        let naProType = schema.entityTypes.find(naProType => naProType.code == naPro.navigationName);
                        this.convertNewEntityType(naProType, schema).forEach(ele => {
                            if (ele.parentItem != null) {
                                ele.parentItem = naPro.code + "." + ele.parentItem;
                            } else {
                                ele.parentItem = naPro.code;
                            }
                            ele.text = naPro.name + "." + ele.text;
                            propertyList.push(ele);
                        });
                    }
                    break;
            }
        })
        return propertyList;
    }


    convertNewEntitySet(set: NewEntitySet, schema: NewSchema): any {
        let propertyList = Array<{ text: string, propertyDataType: PropertyDataType, value: string, parentItem: string }>();
        let type = schema.entityTypes.find(type => type.code == set.entityType);
        type.properties.forEach(pro => {
            let propertyListItem =
            {
                value: pro.code,
                text: pro.name,
                propertyDataType: this.convertNewDataType2PropertyDataType(pro.dataType),
                parentItem: null
            };
            propertyList.push(propertyListItem);
        });
        if (type.navigationProperties.length != 0) {
            type.navigationProperties.forEach(naPro => {
                switch (naPro.navigationType) {
                    case NewNavigationType.EntitySet:
                        {
                            let naProSet = schema.entitySets.find(naProSet => naProSet.code == naPro.navigationName);
                            this.convertNewEntitySet(naProSet, schema).forEach(ele => {
                                    if (ele.parentItem != null) {
                                        ele.parentItem = naPro.code + "." + ele.parentItem;
                                    } else {
                                        ele.parentItem = naPro.code;
                                    }
                                    ele.text = naPro.name + "." + ele.text;
                                    propertyList.push(ele);
                            });
                        }
                        break;
                    case NewNavigationType.EntityType:
                        {
                            let naProType = schema.entityTypes.find(naProType => naProType.code == naPro.navigationName);
                            this.convertNewEntityType(naProType, schema).forEach(ele => {
                                if (ele.parentItem != null) {
                                    ele.parentItem = naPro.code + "." + ele.parentItem;
                                } else {
                                    ele.parentItem = naPro.code;
                                }
                                ele.text = naPro.name + "." + ele.text;
                                propertyList.push(ele);
                            });
                        }
                        break;
                }
            })
        }
        return propertyList;
    }

    /**
     * 
     * @param newType ç»ä¸Schemaçå±æ§
     * @returns 
     */
    convertNewDataType2PropertyDataType(newType: NewDataType): PropertyDataType {
        let type = PropertyDataType.String;
        switch (newType) {
            case NewDataType.Boolean:
                type = PropertyDataType.Boolean;
                break;
            case NewDataType.Byte:
                type = PropertyDataType.Byte;
                break;
            case NewDataType.Date:
                type = PropertyDataType.DateTime;
                break;
            case NewDataType.DateTime:
                type = PropertyDataType.DateTime;
                break;
            case NewDataType.Decimal:
                type = PropertyDataType.Decimal;
                break;
            case NewDataType.Integer:
                type = PropertyDataType.Int32;
                break;
            case NewDataType.String:
                type = PropertyDataType.String;
                break;
        }
        return type;
    }

}